"use client";

import { useState } from "react";
import {
  Edit3,
  Trash2,
  Check,
  X,
  Plus,
  DollarSign,
} from "lucide-react";

// Peso formatter
const currency = (val = 0) =>
  new Intl.NumberFormat("en-PH", {
    style: "currency",
    currency: "PHP",
    minimumFractionDigits: 2,
  }).format(val);

export default function TransactionTable({
  transactions = [],
  loading,
  onAddTransaction,
  onEditTransaction,
  onDeleteTransaction,
}) {
  /* ===========================
     STATE
  ============================ */
  const [showForm, setShowForm] = useState(false);
  const [editingId, setEditingId] = useState(null);

  const [form, setForm] = useState({
    date: "",
    description: "",
    deposit: "",
    withdrawal: "",
  });

  const [rowForm, setRowForm] = useState({
    date: "",
    description: "",
    deposit: "",
    withdrawal: "",
  });

  /* ===========================
     HANDLERS
  ============================ */
  const handleAddSubmit = (e) => {
    e.preventDefault();

    if (!form.date || (!form.deposit && !form.withdrawal)) {
      alert("Date and amount are required.");
      return;
    }

    onAddTransaction({
      date: form.date,
      description: form.description,
      deposit: Number(form.deposit || 0),
      withdrawal: Number(form.withdrawal || 0),
    });

    setForm({ date: "", description: "", deposit: "", withdrawal: "" });
    setShowForm(false);
  };

  const startEdit = (t) => {
    setEditingId(t.id);
    setRowForm({
      date: t.date,
      description: t.description,
      deposit: t.deposit || "",
      withdrawal: t.withdrawal || "",
    });
  };

  const saveEdit = (id) => {
    if (!rowForm.date || (!rowForm.deposit && !rowForm.withdrawal)) {
      alert("Date and amount are required.");
      return;
    }

    onEditTransaction(
      {
        date: rowForm.date,
        description: rowForm.description,
        deposit: Number(rowForm.deposit || 0),
        withdrawal: Number(rowForm.withdrawal || 0),
      },
      id
    );

    setEditingId(null);
  };

  /* ===========================
     DERIVED VALUES
  ============================ */
  const totalDeposits = transactions.reduce(
    (sum, t) => sum + (t.deposit || 0),
    0
  );

  const totalWithdrawals = transactions.reduce(
    (sum, t) => sum + (t.withdrawal || 0),
    0
  );

  const currentBalance = totalDeposits - totalWithdrawals;

  /* ===========================
     RENDER
  ============================ */
  return (
    <div className="space-y-6">

      {/* SUMMARY */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
        <SummaryCard label="Total Deposits" value={currency(totalDeposits)} />
        <SummaryCard label="Total Withdrawals" value={currency(totalWithdrawals)} />
        <SummaryCard label="Current Balance" value={currency(currentBalance)} />
      </div>

      {/* TABLE CONTAINER */}
      <div className="bg-white border rounded-xl shadow">

        {/* HEADER */}
        <div className="flex justify-between items-center p-4 border-b">
          <h2 className="text-lg font-semibold">Transaction Records</h2>
          <button
            onClick={() => setShowForm(!showForm)}
            className="flex items-center gap-2 bg-blue-600 text-white px-4 py-2 rounded hover:bg-blue-700"
          >
            <Plus size={16} /> Add Transaction
          </button>
        </div>

        {/* ADD FORM */}
        {showForm && (
          <form
            onSubmit={handleAddSubmit}
            className="grid grid-cols-1 md:grid-cols-5 gap-4 p-4 bg-gray-50 border-b"
          >
            <input
              type="date"
              value={form.date}
              onChange={(e) => setForm({ ...form, date: e.target.value })}
              className="border rounded px-3 py-2"
              required
            />
            <input
              type="text"
              placeholder="Description"
              value={form.description}
              onChange={(e) => setForm({ ...form, description: e.target.value })}
              className="border rounded px-3 py-2"
            />
            <input
              type="number"
              placeholder="Deposit"
              value={form.deposit}
              onChange={(e) => setForm({ ...form, deposit: e.target.value })}
              className="border rounded px-3 py-2"
            />
            <input
              type="number"
              placeholder="Withdrawal"
              value={form.withdrawal}
              onChange={(e) => setForm({ ...form, withdrawal: e.target.value })}
              className="border rounded px-3 py-2"
            />
            <button
              type="submit"
              className="bg-green-600 text-white rounded flex items-center justify-center gap-2"
            >
              <Check size={16} /> Save
            </button>
          </form>
        )}

        {/* TABLE */}
        <div className="overflow-x-auto">
          <table className="w-full text-sm">
            <thead className="bg-gray-100">
              <tr>
                <th className="p-3 text-left">Date</th>
                <th className="p-3 text-left">Description</th>
                <th className="p-3 text-right">Deposit</th>
                <th className="p-3 text-right">Withdrawal</th>
                <th className="p-3 text-right">Balance</th>
                <th className="p-3 text-center">Actions</th>
              </tr>
            </thead>

            <tbody>
              {transactions.map((t) => (
                <tr key={t.id} className="border-t hover:bg-gray-50">

                  {editingId === t.id ? (
                    <>
                      <td className="p-3">
                        <input
                          type="date"
                          value={rowForm.date}
                          onChange={(e) =>
                            setRowForm({ ...rowForm, date: e.target.value })
                          }
                          className="border rounded px-2 py-1 w-full"
                        />
                      </td>

                      <td className="p-3">
                        <input
                          value={rowForm.description}
                          onChange={(e) =>
                            setRowForm({ ...rowForm, description: e.target.value })
                          }
                          className="border rounded px-2 py-1 w-full"
                        />
                      </td>

                      <td className="p-3 text-right">
                        <input
                          type="number"
                          value={rowForm.deposit}
                          onChange={(e) =>
                            setRowForm({ ...rowForm, deposit: e.target.value })
                          }
                          className="border rounded px-2 py-1 w-full text-right"
                        />
                      </td>

                      <td className="p-3 text-right">
                        <input
                          type="number"
                          value={rowForm.withdrawal}
                          onChange={(e) =>
                            setRowForm({ ...rowForm, withdrawal: e.target.value })
                          }
                          className="border rounded px-2 py-1 w-full text-right"
                        />
                      </td>

                      <td className="p-3 text-right font-semibold">
                        {currency(t.balance)}
                      </td>

                      <td className="p-3 flex justify-center gap-2">
                        <button
                          onClick={() => saveEdit(t.id)}
                          className="text-green-600"
                        >
                          <Check size={16} />
                        </button>
                        <button
                          onClick={() => setEditingId(null)}
                          className="text-gray-500"
                        >
                          <X size={16} />
                        </button>
                      </td>
                    </>
                  ) : (
                    <>
                      <td className="p-3">{t.date}</td>
                      <td className="p-3">{t.description || "-"}</td>
                      <td className="p-3 text-right text-green-600">
                        {t.deposit ? currency(t.deposit) : "-"}
                      </td>
                      <td className="p-3 text-right text-red-600">
                        {t.withdrawal ? currency(t.withdrawal) : "-"}
                      </td>
                      <td className="p-3 text-right font-bold">
                        {currency(t.balance)}
                      </td>
                      <td className="p-3 flex justify-center gap-2">
                        <button
                          onClick={() => startEdit(t)}
                          className="text-yellow-600"
                        >
                          <Edit3 size={16} />
                        </button>
                        <button
                          onClick={() => onDeleteTransaction(t.id)}
                          className="text-red-600"
                        >
                          <Trash2 size={16} />
                        </button>
                      </td>
                    </>
                  )}
                </tr>
              ))}

              {!transactions.length && (
                <tr>
                  <td colSpan="6" className="p-6 text-center text-gray-500">
                    <DollarSign className="mx-auto mb-2 opacity-40" />
                    No transactions found
                  </td>
                </tr>
              )}
            </tbody>
          </table>

          {loading && (
            <div className="p-4 text-center text-gray-500">Loading...</div>
          )}
        </div>
      </div>
    </div>
  );
}

/* ===========================
   SUMMARY CARD
=========================== */
function SummaryCard({ label, value }) {
  return (
    <div className="p-4 border rounded-xl bg-gray-50">
      <p className="text-sm text-gray-600">{label}</p>
      <p className="text-2xl font-bold text-gray-900">{value}</p>
    </div>
  );
}
